import React, { useEffect, useState } from "react";
import axios from "axios";
import pin from "../../../public/assets/Pin.png";
import Image from "next/image";
import avtar from '../../../public/assets/avtar.jpg';
interface Project {
  _id: string;
  name: string;
  description: string;
  // Add other properties if needed
}

const PinnedProjects = () => {
  const [pinnedProjects, setPinnedProjects] = useState<Project[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchPinnedProjects = async () => {
      try {
        // Retrieve token from localStorage (if using authentication)
        const token = localStorage.getItem("token"); 

        // Make an API call to the backend to fetch pinned projects
        const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/projects/pinned`, {
          headers: {
            Authorization: `Bearer ${token}`, // Send token in headers if required
          },
        });
        // console.log(token);
        // Set the data received from backend
        setPinnedProjects(response.data.projects || []); // Adjust key according to backend response
      } catch (err) {
        console.error("Error fetching pinned projects:", err);
        setError("Failed to load pinned projects.");
      } finally {
        setLoading(false);
      }
    };

    fetchPinnedProjects();
  }, []);

  // Handle loading state
  if (loading) return <p>Loading pinned projects...</p>;

  // Handle error state
  
  if (error) return <p className="text-red-500">{error}</p>;

  const EmptyState = () => (
    <div className="flex flex-col items-center justify-center bg-white rounded-lg">
      <div className="mb-4">
        <div className="bg-[#EFF1FF] rounded-full p-8 mb-4">
          <img
            src="/assets/illustration.png"
            alt="No Project Found"
            className="w-24 h-24"
          />
        </div>
      </div>
      <h3 className="text-gray-700 text-xl font-sm mb-4">
        No Pinned Projects
      </h3>
    </div>
  );

  if (!pinnedProjects.length) {
    return <EmptyState />;
  }

  return (
    <div>
      {/* {pinnedProjects.length > 2 ? ( */}
        <div className="flex space-x-6 overflow-x-auto">
          {pinnedProjects.map((project) => (
            <div
              key={project._id} // Use unique key from backend
              className="bg-white rounded-lg p-4 w-80 shadow-xl border border-gray-200 hover:shadow-md relative"
              // className="block max-w-sm p-6 bg-white border border-gray-200 rounded-lg shadow-sm hover:bg-gray-100  dark:border-gray-700 dark:hover:bg-gray-700"
            >
              {/* Pin Icon */}
              <div className="absolute top-4 right-4">
                <Image src={pin} alt="Pin Icon" width={24} height={24} />
              </div>

              {/* Project Content */}
              <h3 
              // className="mb-2 text-2xl font-bold tracking-tight text-gray-900 "
              className="text-xl font-semibold text-gray-800 mb-2"
              >{project.name}</h3>
              <p className="text-sm text-gray-600">{project.description}</p>
              <div className=" bottom-4 left-4">
                  <Image
                    src={avtar}
                    alt={`${project.name} Profile`}
                    width={32}
                    height={32}
                    className="rounded-full border border-gray-200"
                  />
                </div>
              {/* Profile Photo or Placeholder */}
              {/* {project.projectImage && ( */}
                
              {/* // )} */}
            </div>
          ))}
        </div>
      {/* ) : (
        <p>No pinned projects available.</p>
      )} */}
    </div>
  );
};

export default PinnedProjects;
