'use client';

import { useState } from 'react';
import PinnedProjects from '@/components/dashboard/PinnedProjects';
import RecentTodos from '@/components/dashboard/RecentTodos';
import StuffDueSoon from '@/components/dashboard/StuffDueSoon';
import RecentChats from '@/components/dashboard/RecentChats';
import Schedule from '@/components/dashboard/Schedule';
import CreateNewProject from "@/components/dashboard/Create_Project_modal";
import InviteTeam from '@/components/dashboard/InviteTeam';
import Header from '../../../components/layout/Header'
interface DashboardPageProps {
  userName: string;
}

export default function DashboardPage({ userName = "Esther Howard" }: DashboardPageProps) {
  const [isNewProjectOpen, setIsNewProjectOpen] = useState(false);
  const [isInvite, setisInvite]= useState(false);
  const openNewProject = () => {
    setIsNewProjectOpen(true);
  };

  const closeNewProject = () => {
    setIsNewProjectOpen(false);
  };

  const handleInvite=()=>{
    setisInvite(true);
  };
  const closeInvite=()=>{
    setisInvite(false);
  }

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return "Good Morning";
    if (hour < 18) return "Good Afternoon";
    return "Good Evening";

  };

  return (
    <div className="flex bg-gray-50">
      {/* Main Content */}
      <div className="flex-1 flex flex-col bg-[#F8F7FA]">
            <div className="mt-4">
              <Header />
            </div>
           
      <div className="flex-1">
        {/* Dashboard Content */}
        <div className="p-6">
          {/* Header */}
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-2xl font-semibold">
              {getGreeting()}, {userName}
            </h1>
            <div className="space-x-3">
              <button
                className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4A4495] transition-colors"
                onClick={openNewProject}
              >
                + New Project
              </button>
              <button className="px-4 py-2 border border-[#D1D5DB] text-[#5D56BD] bg-[#F9FAFB] rounded-lg hover:bg-[#EFEFFB] transition-colors"
              onClick={handleInvite}>
                Invite Team
              </button>
            </div>
          </div>

          {/* Grid Layout */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Pinned Projects Section */}
            <div className="lg:col-span-2">
              <div className="bg-white rounded-lg p-6 shadow-sm">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-lg font-medium">Pinned Projects</h2>
                  <button className="text-[#5D56BD] text-sm hover:text-[#4A4495]">
                    Show all
                  </button>
                </div>
                <PinnedProjects />
              </div>
            </div>

            {/* Recent To-dos Section */}
            <div className="bg-white rounded-lg p-6 shadow-sm">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-lg font-medium">Recent To-Do's</h2>
              </div>
              <RecentTodos />
            </div>

            {/* Stuff Due Soon Section */}
            <div className="bg-white rounded-lg p-6 shadow-sm">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-lg font-medium">Stuff Due Soon</h2>
              </div>
              <StuffDueSoon />
            </div>

            {/* Recent Chats Section */}
            <div className="bg-white rounded-lg p-6 shadow-sm">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-lg font-medium">Recent Chats</h2>
              </div>
              <RecentChats />
            </div>

            {/* My Schedule Section */}
            <div className="bg-white rounded-lg p-6 shadow-sm">
              <div className="flex justify-between items-center mb-4">
                {/* <h2 className="text-lg font-medium">My Schedule</h2> */}
                {/* <button className="text-[#5D56BD] text-sm hover:text-[#4A4495]">
                  Show all
                </button> */}
              </div>
              <Schedule />
            </div>
          </div>
          </div>
        </div>
      </div>

      {/* Create New Project Modal */}
      {isNewProjectOpen && <CreateNewProject onClose={closeNewProject} />}
      {isInvite && <InviteTeam onClose={closeInvite}/>}
    </div>
  );
}